import { Component } from '@angular/core';
import { PitBlockComponent } from './pit-block/pit-block.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  player1Home = {index:0, value:"0"};
  player2Home = {index:0, value:"0"};
  player1pits = [];
  player2pits = [];
  title = 'swagger-ui';
  gameStarted = false;
  gameData = {
    id: 0
  };
  currentPlayer= "Player 1";

  constructor() { }

  ngOnInit(): void {
    this.processPitData({
      "1": 9,
      "2": 3,
      "3": 2,
      "4": 0,
      "5": 9,
      "6": 9,
      "7": 3,
      "8": 9,
      "9": 0,
      "10": 2,
      "11": 4,
      "12": 10,
      "13": 9,
      "14": 3
    });
  }

  processPitData(pitData:Object){
    var attribs = Object.getOwnPropertyNames(pitData);
    for (let i = 0; i < attribs.length; i++) {
      const attrib = attribs[i];
      if(i < 6){
        this.player1pits.push({"index":attrib, "value":pitData[attrib]})
      } else if(i == 6){
        this.player1Home = {
          'index':Number(attrib), 
          'value':pitData[attrib]
        }
      } else if(i > 6 && i < 13){
        this.player2pits.push({"index":attrib, "value":pitData[attrib]})
      } else {
        this.player2Home = {
          'index':Number(attrib), 
          'value':pitData[attrib]
        }
      }
    }
  }

  getStonesOfPit(pit: {"index": Number, "value": string}){
    return Array(pit.value);
  }

  selectedPit(pit: {"index": Number, "value": string}){
    // Fire event for number
  }

}
